body {
    margin: 0;
    padding: 0;
}
.templateDiv {
    height: 500px;
    /*outline: 1px solid red;*/
    margin: 0 30px;

}

.templateTitle {
    font: 30px "Agency FB";
    color: #518F70;
    text-transform: uppercase;
}

.templateText {
    font: 20px Arial, sans-serif;
    color: #518F70;
}
/*--------------------*/
#container{
    min-height: 100vh;
    overflow: hidden;
    display: block;
    position: relative;
    padding-bottom: 88px;
    font: 20px Arial, sans-serif;
    color: #518F70;
}

.logo {
    width: 139px;
    height: 66px;
    margin: 25px 0 25px 70px;
}

.upperDiv {
    background: #A3E5C4;
}

.bottomDiv {
    width: 100%;
    padding: 10px 70px;
    position: absolute;
    bottom: 0;
    background: #A3E5C4;
}

.midDiv {

}

.bottomDiv > p {
    color: #fff;
    margin: 20px 0;
    font-size: 24px;
    font-family: "Agency FB";
}

a.forBtn, a.forBtn:visited {
    font-family: "agency FB", sans-serif;
    text-decoration: none;
    border: 1px solid #518F70;
    color: #518f70;
    padding: 7px 5px;
    display: block;
    width: 100px;
    text-align: center;
    border-radius: 7px;
    cursor: pointer;
}
