
body {
    margin: 0;
    padding: 0;
}
.templateDiv {
    height: 500px;
    /*outline: 1px solid red;*/
    margin: 0 30px;

}

.templateTitle {
    font: 30px "Agency FB";
    color: #518F70;
    text-transform: uppercase;
}

.templateText {
    font: 20px Arial, sans-serif;
    color: #518F70;
}
/*--------------------*/
#container{
    min-height: 100vh;
    overflow: hidden;
    display: block;
    position: relative;
    padding-bottom: 88px;
    font: 20px Arial, sans-serif;
    color: #518F70;
}

.logo {
    width: 139px;
    height: 66px;
    margin: 25px 0 25px 70px;
}

.upperDiv {
    background: #A3E5C4;
}

.bottomDiv {
    width: 100%;
    padding: 10px 70px;
    position: absolute;
    bottom: 0;
    background: #A3E5C4;
}

.midDiv {

}

.bottomDiv > p {
    color: #fff;
    margin: 20px 0;
    font-size: 24px;
    font-family: "Agency FB";
}

a.forBtn, a.forBtn:visited {
    font-family: "agency FB", sans-serif;
    text-decoration: none;
    border: 1px solid #518F70;
    color: #518f70;
    padding: 7px 5px;
    display: block;
    width: 100px;
    text-align: center;
    border-radius: 7px;
    cursor: pointer;
}


input {
    display: block;
    background: lightgray;
    color: #666;
    padding: 20px;
    font: 24px arial, sans-serif;
    border: 1px solid lightgray;
    border-radius: 3px;
    margin: 15px  0px;
}

.welcomeScreen {
    margin: 30px auto;
    width: 400px;
    display: flex;
    flex-direction: column;
    padding: 40px;
    border: 1px solid  #A3E5C4;
    border-radius: 3px;
    align-items: center;
    align-content: center;
    justify-content: center;
    text-align: center;

}

.button{
    padding: 20px;
    font: 24px "Agency FB";
    background: #A3E5C4;
    border: 1px solid #A3E5C4;
    border-radius: 3px;
    text-align: center;
    color: white;
    margin: 10px;
    min-width: 100px;
    cursor: pointer;
}


.signUp {
    color: #A3E5C4;
    background: white;
    display: block;
    margin: auto;
}

.error {
    color: crimson;
}



.wrapper {
    display: flex;
    height: 100vh;
}

.navigation {
    background: #A2AEA8;
    width: 300px;
}

.navigation > ul > li > a {
    text-decoration: none;
    font-family: "Agency FB";
    font-size: 30px;
    color: white;
}

.navigation > ul > li {
    list-style: none;
}
navWrapper {
    background: #000;
}
.box {
    font-family: "agency FB", sans-serif;
}

.content {
    margin: 0px 40px 20px 0px;
}

ul.recTransactions {
    background: #a3e5c4;
    padding: 1px;
    margin: 0;
    border-radius: 10px;
}

ul.recTransactions > li {
    list-style: none;
    margin: 20px;
    padding: 20px;
    background: #91CBAE;
    border-radius: 10px;
    display: flex;
}

ul.recTransactions > li p {
    margin: 0;
    text-transform: uppercase;
}

ul.recTransactions > li p:nth-child(2) {
    font-family: arial, sans-serif;
    font-size: 12px;
    text-transform: lowercase;
    font-style: italic;
    font-weight: bold;
    color: #fff;
}

ul.recTransactions > li > div {
    margin-right: 20px;
    min-width: 170px;
}

ul.recTransactions > li> span {
    color: red;
}

.contentWrapper {
    display: flex;
    flex-direction: row;
}
.transactionTbl {
    font-family: "Agency FB", sans-serif;
    border-spacing: 0;
    text-align: left;
    text-transform: uppercase;
}

.transactionTbl tr:nth-child(1) {
    background: #C7F5DE;
    font-weight: normal;
}

.transactionTbl tr:nth-child(1) > th:nth-child(even) {
    background: #91CBAE;
}


.transactionTbl td, .transactionTbl th{
    padding: 5px 25px;
}

.transactionTbl th{

}
.transactionTbl tbody > tr:nth-child(odd) {
    background: #91CBAE;
}
.transactionTbl tbody > tr:nth-child(odd) > td:nth-child(even) {
    background: #86bca1;
}

.transactionTbl tbody > tr:nth-child(even) {
    background: #a3e5c4;
}
.transactionTbl tbody > tr:nth-child(even) > td:nth-child(even) {
    background: #91cbae;
}

/*transaction form*/

form.transactionForm > input {
    display: block;
    background: lightgray;
    color: #000;
    line-height: 0;
    font: 17px "agency fb", sans-serif, bold;
    border: 1px solid transparent;
    border-radius: 0;
    margin: 0;
    padding: 0 5px;
    outline-color: rgb(81, 143, 112);
    text-transform: uppercase;
    text-align: center;
}

form.transactionForm > input:focus  {
    outline-color: rgb(81, 143, 112);
}
form.transactionForm {
    display: flex;
    flex-direction: row;
    padding: 0;
    background: #c7f5de;
    margin: 0;
}

button.addTransBtn {
    font: 19px "agency FB", sans-serif;
    text-decoration: none;
    border: 1px solid #518F70;
    color: #518f70;
    padding: 5px 5px;
    display: block;
    text-align: center;
    border-radius: 6px;
    background: #fff;
    margin-bottom: 35px;
    cursor: pointer;
}
button.addTransBtn:focus {
    outline-color: transparent;
}

.addTransDiv {
    margin: 60px 0 20px;
}

button.submit {
    padding: 0px;
    font: 25px "Agency FB";
    background: #A3E5C4;
    border: 1px solid #A3E5C4;
    border-radius: 3px;
    text-align: center;
    color: white;
    margin: 5px;
    min-width: 100px;
}
